@import url(https://fonts.googleapis.com/css2?family=Alef:wght@400;700&display=swap);
#site-map {
  text-transform: uppercase;
}

#footer {
  grid-area: footer;
  color: #ffffff;
  background-color: #636162;
  display: flex;
  justify-content: center;
  align-items: center;
}

#top-nav {
  color: #000;
  grid-area: top-nav;
  background-color: rgb(255, 255, 255);
  position: relative;
  height: 65px;
  display: flex;
  align-items: center;
  /* margin: 0 3em; */
  justify-content: space-between;
  direction: ltr;
  flex-grow: 1;
}

.top-nav::after {
  content: "";
  display: block;
  position: absolute;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
#top-nav img {
  width: 200px;
}

.wrap {
  display: flex;
  align-items: center;
  /* margin: 0 20px; */
}

.top-page {
  display: flex;
  align-items: center;
  height: 3.125em;
  margin: 20px 0px;
}

.top-page span {
  margin: 0 0.5em;
  font-size: 1rem;
}



td.MuiTableCell-root.MuiTableCell-body.empty-data {
  text-align: center;
  color: #c72561;
  line-height: 10px;
}

td.MuiTableCell-root.MuiTableCell-body.empty-data > span {
  vertical-align: super;
  margin: 0 10px;
  font-size: 20px;
}



.card-container {
  display: flex;
  justify-content: space-evenly;
  flex-grow: 1;
  align-items: center;
}

.card {
  width: 16.875rem;
  height: 12.5rem;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 4px;
  box-shadow: 1px 1px 5px -1px #101010;
  padding: 10px;
  position: relative;
  color: #fff;
  overflow: hidden;
  background-image: linear-gradient(90deg, rgb(209, 83, 0), rgb(238, 181, 104));
}

