.card::before,
.card::after {
  background-color: rgba(238, 181, 104, 0.46);
}

.card::before {
  content: "";
  position: absolute;
  width: 120px;
  height: 120px;
  left: -31px;
  top: 5px;
  border-radius: 50%;
  /* background-color: inherit; */
}

.card::after {
  content: "";
  position: absolute;
  width: 150px;
  height: 150px;
  left: -10px;
  top: 95px;
  border-radius: 50%;
  /* background-color: inherit; */
}

