.money-sign {
  width: 1em;
  height: 1em;
  display: inline-block;
  font-size: 6rem;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  width: 100%;
  text-align: end;
  z-index: 1;
  position: absolute;
  top: 112px;
  left: 20px;
  font-family: monospace;
}
.dollar {
  position: absolute;
  top: 61px;
  left: 122px;
}
.card > h3 {
  position: absolute;
  top: 100px;
  left: 180px;
  font-size: xx-large;
}
.card > header {
  position: absolute;
  top: 40px;
  /* font-family: alef; */
  font-size: 1.5rem;
  right: 35px;
  font-weight: bold;
}



#pages-area {
  grid-area: pages-area;
  background-color: #c4c4c4;
}

.page {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 114px);
  padding: 0 3em;
  background-color: rgb(141, 141, 141);
  justify-content: space-evenly;
}

.action-btn {
  padding: 0.5em 1em;
  border: 0;
  color: #fff;
  background: #6d6d6d;
  border-radius: 5px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
}

#user-info {
  display: flex;
  flex-direction: column;
}

.user-image {
  display: flex;
  justify-content: center;
  margin-top: 3.125em;
  height: 130px;
  width: 130px;
  align-items: center;
  background: #ffffff;
  border-radius: 100%;
  margin: auto;
  /* outline: 1px solid #f50f0f; */
  border-radius: 50%;
  border: 3px solid #636162;
}

.user-image img {
  border: 3px solid #ffffff;
  border-radius: 50%;
  max-height: 100px;
  max-width: 100px;
  /* background-color: #e21717; */
}

.container {
  color: #c8c8c8;
  display: flex;
  justify-content: space-evenly;
  background: rgb(99, 97, 98);
  /* height: 32px; */
  align-items: center;
  margin: 1.25rem 0;
}
.username {
  font: bold 1.2rem "Alef";
  margin-top: 1.25rem;
  color: rgb(97, 97, 98);
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}

.list-wrap {
  display: flex;
  align-items: center;
  width: 140px;
  margin: auto;
}

.list-wrap span {
  text-align: right;
}

.list-wrap svg {
  fill: #636162;
}

/* svg {
  fill: #636162 !important;
} */

#info-panel {
  grid-area: info-panel;
  background-color: rgb(225, 136, 56);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.error-notice {
  position: absolute;
  top: 5px;
  background: #d35351;
  padding: 1em;
  color: #fff;
  margin: 20px auto;
  border-radius: 4px;
  min-height: 27px;
  display: flex;
}

.login-status-icon {
  background-image: url(data:image/png;base64,);
  display: inline-block;
  height: 27px;
  width: 27px;
  margin: 0 5px;
}

.login-status-message {
  height: 27px;
  line-height: 27px;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-size: 100%;
  direction: rtl;
  line-height: 1;
  font-family: Alef, Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(249, 249, 249);
}

a {
  text-decoration: none;
}

main {
  height: 100vh;
  display: grid;
  grid-template-columns: 18em 1fr;
  grid-template-rows: 64px 1fr 50px;
  grid-template-areas:
    "info-panel top-nav"
    "info-panel pages-area"
    "footer footer";
}

::selection {
  background: #768e8f;
  color: #fff;
}
::-moz-selection {
  background: #768e8f;
  color: #fff;
}

img {
  width: 100%;
  height: 100%;
}

@media screen and (max-device-width: 1200px) {
  main {
    grid-template-columns: 170px 1fr;
  }
}

